<template>
  <div id="changename">
    <head-vue title="Change Surname" goBack="true">
      <div slot="setRight" @click="save">Save</div>
    </head-vue>
    <div class="editorName" :class="borderColor? 'active_color':''">
      <input type="text" placeholder="only letters and numbers allowed"
      v-model="value"
      maxlength="16"
      @focus="borderColor = !borderColor"
      @blur="borderColor = !borderColor"
      @input="nameExp(value)">
      <img src="../../assets/me/del@3x.png" alt="" @click="clear">
    </div>
    <div class="text">1-16 characters</div>
    <div class="toast" v-if="toastFalg">
      only letters and numbers allowed
    </div>
  </div>
</template>
<script>
import head from '../../components/common/head'
import {mapActions, mapGetters} from 'vuex'

import {
  modifyUsernameRequest
} from '../../api/method'
import {
  modifyUserInformationResponse
} from '../../api/getData'
export default {
  data () {
    return {
      borderColor: false, // input 获取焦点时border颜色
      value: 'Curise', // 输入内容
      toastFalg: false
    }
  },
  components: {
    'head-vue': head
  },
  computed: {
    ...mapGetters(['getsurName']),
    lastName () {
      return this.getsurName
    }
  },
  created () {
    this.value = this.lastName
    modifyUserInformationResponse(this.NAVIGATOR, this, 'changesurname')
  },
  methods: {
    ...mapActions(['surName']),
    nameExp (val) {
      // 输入内容格式验证
      if (!/^\w{0,}$/.test(val)) {
        this.toastFalg = true
      } else {
        this.toastFalg = false
      }
    },
    save () {
      if (this.value !== '') {
        modifyUsernameRequest(this.NAVIGATOR, this, 'surnam', this.value)
        console.log(this.value)
        if (this.$root.ISDEBUG) {
          window.modifyUserInformationResponse(true)
        }
      } else {
      }
    },
    clear () {
      this.value = ''
    }
  }
}
</script>
<style lang="less" scoped>
#changename{
  .wh(100%, 100%);
  .fs;
  z-index: 999;
  background: white;
  .editorName {
    margin-top: 30px;
    padding: 0 30px;
    .wh-l(100%, 100px);
    border-top: 1px solid #cccccc;
    border-bottom: 1px solid #cccccc;
    display: flex;
    justify-content: space-between;
    input {
      flex: 1;
      font-size: 24px;
      background-color: transparent;
      .wh-l(auto, 100%);
      color: black;
      font-weight: 600px;
    }
    input::-webkit-input-placeholder {
      /* WebKit browsers */
      color: #cccccc;
    }
    img {
      .wh(24px, 24px);
      align-self: center;
    }
  }
  .active_color {
    border-color: #01a29a;
  }
  .text {
    .wh(100%, auto);
    font-size: 20px;
    color: #cccccc;
    text-align: left;
    padding: 20px 30px 0 30px;
  }
  .toast{
    .wh(400px, 108px);
    line-height: 135px;
    background: url(../../assets/me/spring-@3x.png);
    background-size: cover;
    color: white;
    border-radius: 10px;
    margin: 0 auto;
    margin-top: 40px;
    position: relative;
    font-size: 24px;
  }
}
</style>



